//response 拦截器
service.interceptors.response.use(
  response => {
    /**
     * code为非20000是抛错 可结合自己业务进行修改
     */
    // 302	登录已失效
    if (response.status === 302) {
      // if (res.data && res.data.extra === 1) {
      // confirmMessage(`您的账号在别处登录。如非本人操作，则密码可能已泄漏，建议修改密码`)
      // }
      confirmMessage('登录失效，重新登录')
      clearCacheEnterLogin()
      router.push('/login')
    } else {
      return response
    }
  },
  error => {
    if (error.response) {
      const response = error.response
      if (response.status == 500) {
        errorMessage('网络错误，请检查您的网络')
      } else if (response.data && response.data.msg) {
        errorMessage(response.data.msg)
      }
    }
    return Promise.reject(error)
  }
)
// 登录请求方法
const loginreq = (method, url, params) => {
    return axios({
        method: method,
        url: url,
        headers: {
            'Content-Type': 'application/x-www-form-urlencoded',
        },
        data: params,
        traditional: true,
        transformRequest: [
            function(data) {
                let ret = ''
                for (let it in data) {
                    ret +=
                        encodeURIComponent(it) +
                        '=' +
                        encodeURIComponent(data[it]) +
                        '&'
                }
                return ret
            }
        ]
    }).then(res => res.data);
};
// 通用公用方法
const req = async (method, url, params) => {
    const res = await service({
    method: method,
    url: url,
    headers: {
      'Content-Type': 'application/json',
      token: localStorage.getItem('logintoken')
    },
    data: params
  });
  return res;
};
// 通用公用方法
const reqFile = async (method, url, params) => {
  const res = await service({
  method: method,
  url: url,
  headers: {
    'Content-Type': 'application/json',
    token: localStorage.getItem('logintoken')
  },
  data: params,
  responseType:"blob"
});
return res;
};


export {
    loginreq,
    req,
    reqFile
}



